import React,{useState} from "react";
import { withStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";
import Link from '@material-ui/core/Link';
import { useHistory } from "react-router-dom";


const styles = (theme) => ({

    root: {
        margin: 0,
        padding: theme.spacing(2),
        
            
      },

  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: "red"
    },
   
});


const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    // <MuiDialogTitle disableTypography className={classes.root} {...other}>
    //   <Typography variant="h6">{children}</Typography>
     
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) 
    // </MuiDialogTitle>
 
});

const DialogContent = withStyles(() => ({
  root: {
    textAlign:'center'
   
  }
}))(MuiDialogContent);



export default function JobConfirmationPopUp(props) {
 
    const [open, setOpen] = React.useState(true);
    let history = useHistory();
   const[jobList,setJobList]= useState(props.jobList)
    
    const handleClose = () => {
      props.onClose();
      setOpen(false);
  };

  return (
    <div>
     
      <Dialog 
  
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle
          id="customized-dialog-title"
          onClose={handleClose}
        ></DialogTitle>
        <DialogContent>
        <img src="./success.svg"  />
       <p style={{color:"#609E2E",fontSize:"25px"}}>Confirmed</p>
    
       <div style={{color:"black",fontWeight:"bold"}}>
           <div > Approval Request has been successfully sent to DT Admin</div>
           <div> You will be notified once it gets approved</div>
           <br/>
       
       </div>
       {/* <div > 
       <div style={{color:"grey"}}>Kindly note the numbers for any future communications</div>
         {jobList.map(job => (
            <>
             <div style={{color:"black",fontWeight:"bold"}} key={job}>Job Number #{job}</div>
           
             </>
        ))}  
       </div>  */}
   
   <p style={{color:"grey"}}>View the jobs in  <Link color="secondary" onClick={  ()=>history.push("/myJobs",{tabSelected:1})
     }> Pending Jobs </Link> </p>
     <p style={{color:"grey"}}>Check the approval status in  <Link color="secondary" onClick={
         ()=>history.push("/myJobs",{tabSelected:2})
     }> Active Jobs </Link>  page  and start job </p>
        </DialogContent>
       
      </Dialog>
    </div>
  );
}